.post-topic {
    margin: 0 4px;
}

body {
    font-family: 'Lato', sans-serif;
}

.app-wrapper {
    display: grid;
    justify-content: center;
}

.logo-main, .recent-posts {
    display: grid;
    justify-items: center;
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

$color-gray-one : #666666;
$color-gray-two: #b3b3b3;
$color-blue-one: #0079FF;
$color-gray-three: #D8D8D8;

.no-content {
    color: $color-gray-one;
    font-size: 14px;
    line-height: 17px;
}

@import './mixins.scss';
@import './home.scss';
@import './searchBar.scss';
@import './recentPosts.scss';
@import './results';
@import './post';